import React from 'react'
import {MDBIcon} from 'mdb-react-ui-kit';

const ArticlesList = (props) => {
  return (
    <div className='body_category'>

                  <MDBIcon onClick={()=>props.setDisplay(-1)} style={{marginBottom: '50px', cursor: 'pointer'}} fas size="2x" icon="arrow-circle-left" />

                <div className="category">
                  
                  <h1 className='category_name'>{props.data[props.display].name}</h1>
                  <div className="category_articles">
                    {props.data[props.display].articles.map((article)=>(
                      <div key={article.name} className="article">
                        <div className="article_container">
                        <span className="article_name">{article.name}</span>
                        <span className="article_description">{article.description}</span>
                        </div>
                        <span className="article_price">{article.price}</span>
                      </div>
                    ))}
                  </div>
                </div>
              </div>
  )
}

export default ArticlesList