import React,{useState,useEffect} from 'react';
import axios from 'axios';
import GridLoader from "react-spinners/GridLoader";
import Header from './Header';
import Footer from './Footer';
import CategoriesList from './CategoriesList';
import ArticlesList from './ArticlesList';

const Home = () => {
  
  const [data,setData] = useState(null)
  const [display,setDisplay] = useState(-1)

    const fetchData = async () =>{
      const url = 'https://aftereight.herokuapp.com/data'
      await axios.get(url)
          .then(response => {
              console.log(response.data)
              setData(response.data)})
  }

    useEffect(()=>{
      fetchData();
    },[])
  
    useEffect(()=>{
      window.scrollTo(0, 0)
    },[display])
  
  
  
    return (
    <div className="Container">
      
        <Header setDisplay={setDisplay} />
        
        <div className="body">
          {data !== null ? 
            (display === -1 ? 
            <CategoriesList setDisplay={setDisplay} data = {data} /> : 
            <ArticlesList data = {data} setDisplay={setDisplay} display = {display} />
            )  
          :
          <GridLoader color="green" css={`position: absolute; left: 40%;`} />
        }
        </div>

       <Footer/> 
      
    </div>
  )
}

export default Home



